<template>
  <header class="header">
    <div class="header__logo">
      <Icon
        name="logo"
        width="46px"
        height="46px"
      />
      <Icon
        name="logo-title"
      />
    </div>
    <div class="header__menu">
      <Button
        v-for="item in routes"
        :key="item.name"
        :type="activeRoute === item.name ? 'primary' : 'white'"
        class="header__menu__link"
        @click="linkClick(item)"
      >
        <Text weight="800">{{ item.title }}</Text>
      </Button>
      <div class="header__menu__divider" />
      <Button
        class="header__menu__contact"
        @click="onContactSupport"
        @mouseover="toggleShowEmail(true)"
        @mouseleave="toggleShowEmail(false)"
      >
        <Text weight="800">{{ isShowEmail ? contactEmail : 'Contact' }}</Text>
      </Button>
    </div>
  </header>
</template>

<script>
import {
  useRoute,
  useRouter
} from 'vue-router'
import Icon from '@/components/UIKit/Icon'
import Button from '@/components/UIKit/Button'
import Text from '@/components/UIKit/Text/Text'
import { computed, reactive, ref } from 'vue'
import { ROUTES_NAMES } from '@/common/routes'
import { useBreakpointsMedia } from '@/plugins/breakpoints'

export default {
  name: 'Header',
  components: {
    Text,
    Button,
    Icon
  },
  setup () {
    const route = useRoute()
    const router = useRouter()
    const { breakpoints } = useBreakpointsMedia()

    const contactEmail = ref('hello@feral.ae')
    const isShowEmail = ref(false)
    const routes = reactive([
      {
        name: ROUTES_NAMES.ABOUT,
        title: 'About'
      },
      {
        name: ROUTES_NAMES.PORTFOLIO,
        title: 'Portfolio'
      }
    ])

    const activeRoute = computed(() => route.name)

    const linkClick = item => {
      if (item.name) {
        router.push({
          name: item.name
        })
      }
    }
    const toggleShowEmail = flag => {
      isShowEmail.value = flag
    }
    const onContactSupport = () => {
      window.location.href = 'mailto:mail@example.org'
    }

    return {
      routes,
      breakpoints,
      isShowEmail,
      activeRoute,
      contactEmail,
      linkClick,
      toggleShowEmail,
      onContactSupport
    }
  }
}
</script>

